@import 'bootstrap/custom/style.loader';
@import 'bootstrap/custom/style.normal';



// small screens
@media only screen and (max-width: 349px) {}



// portrait - phones
@media only screen and (max-width: 543px) {
    @import 'bootstrap/custom/style.phone';
}



// landscape - phones
@media only screen and (min-width: 544px) {
    body {

    }

    .body {
        background-image: url(https://source.unsplash.com/collection/765594/800x1240);
    }



    @import 'bootstrap/custom/style.tablets';
}

@media only screen and (max-width: 767px) {
    body {
        font-size: 14px;
    }

}

// medium - tablets
@media only screen and (min-width: 768px) {
    body {
        font-size: 16px;
        //padding-top: 50px;
        &.has-fixed {

        }
    }

    .body {
        background-image: url(https://source.unsplash.com/collection/765594/1920x1080);
    }

    .modal {
        text-align: center;
        padding: 0$im;
        &:before {
            content: '';
            display: inline-block;
            height: 100%;
            vertical-align: middle;
            margin-right: -4px;
        }
    }
    .modal-dialog {
        display: inline-block;
        text-align: left;
        vertical-align: middle;
    }
    footer {
        font-size: 14px;
    }

    section {
        &.pad {
            .container,
            .container-fluid {
                padding-top: 100px;
                padding-bottom: 100px;
            }
        }

    }


    .fonb {
        .form-group {
            margin-bottom: 30px;
        }
    }

    .border-right {
        border-right: 1px solid #f04;
    }

    .border-left {
        border-left: 1px solid #f04;
    }

    .border-left,
    .border-right {
        &.bc-wash {
            border-color: rgba(255,0,64,0.25);
        }
        &.bc-wwash {
            border-color: rgba(255,255,255,0.15);
        }
        &.bc-bwash {
            border-color: rgba(0,0,0,0.15);
        }
    }

    @import 'bootstrap/custom/style.laptop';
}

// large - laptops
@media only screen and (min-width: 992px) {
    section {

    }


    @import 'bootstrap/custom/style.large';
}

// Extra large - desktops
@media only screen and (min-width: 1200px) {
    body {
        //padding-top: 100px;
        &.has-fixed {
        }
    }


    @import 'bootstrap/custom/style.xlarge';
}
